<template>
    <div>
         <van-tabs v-model="active">
            <van-tab title="全部" to = "/orderlist/orderall"></van-tab>
            <van-tab title="代付款" to = "/orderlist/orderpay"></van-tab>
            <van-tab title="待收货" to = "/orderlist/orderget"></van-tab>
            <van-tab title="已完成" to = "/orderlist/orderend"></van-tab>
        </van-tabs>

        <div class="ora_content" v-for="(item,index) in lists" :key="index">
             <div v-if="item.type == 2">
                <van-cell center :title="'订单:'+item.orderId" :value="typelist[item.type]" :label="item.dateTime" />
                <div class="main" v-for="(value,i) in item.shopist" :key="i">
                    <van-row>
                        <van-col span="8">
                            <p>
                                <img :src="value.imgarr" alt="">
                            </p>
                        </van-col>
                        <van-col span="16">
                            <!-- <p class="des">小米cc9pro手机 1亿像素 五摄四闪 10倍混合光学变焦 魔法绿境 6G+128G 全网通</p> -->
                            <p class="des">{{value.name}}</p>
                            <p class="content"><span>共{{value.num}}件商品</span> <span>实付金额：￥{{value.price*value.num}}</span></p>
                        </van-col>
                    </van-row>
                </div>
                <div class="btn">
                        <!-- <p class="btn03">退款申请</p> -->
                        <p class="btn04" @click="onget(index)">确认收货</p>
                </div> 
            </div>

           
         <!-- 没有订单时显示-->
           <div  v-else>
                <div class="shoplist" >
                    <div class="goodsinfo">
                        <p><img src="../../assets/images/toshop.png" alt=""></p>
                        <p>空空如也</p>
                    </div>
                    <!-- 推荐 -->
                    <foryou></foryou>
                </div>

            </div>

        </div>

         <!-- 没有订单时显示-->
            <div v-show=" lists.length ==0  " class="shoplist" >
                <div class="goodsinfo">
                    <p><img src="../../assets/images/toshop.png" alt=""></p>
                    <p>空空如也</p>
                </div>
        
                <foryou></foryou>
            </div>

            
      

    </div>
</template>

<script>
import foryou from "../../components/public/foryou"
  import { Toast } from 'vant';
    export default {
        data(){
            return{
                active:2,
                lists:[],
                typelist:[],
                 foryou:[
                    {
                        src1: require("../../assets/images/foryou01.webp"),
                        src2: require("../../assets/images/foryou02.webp"),
                        des1:"华为 nova 6 SE 手机（20重好礼/7天价保/质保2年） 绮境森林 全网通8GB+128GB",
                        des2:"华为nova6se 手机 幻夜黑 8+128G 全网通 ",
                        price1:1999,
                        price2:2500
                    },
                    {
                        src1: require("../../assets/images/foryou03.webp"),
                        src2: require("../../assets/images/foryou04.webp"),
                        des1:"华为 nova 6 SE 手机（20重好礼/7天价保/质保2年） 绮境森林 全网通8GB+128GB",
                        des2:"华为nova6se 手机 幻夜黑 8+128G 全网通 ",
                        price1:1999,
                        price2:2500 
                    },
                ]
            }
        },
        methods:{
             // 点击已收货
            onget(index){
                this.lists[index].type = 3
                 Toast('商品已确认收货');
            },
        },
        created(){
            let dataList = this.$store.state.order.lists;
            this.lists = dataList.filter(item=>item.type == 2);
            this.typelist = this.$store.state.order.typelist
            console.log(this.lists)
        },
        components:{
            foryou
        }
    }
</script>

<style lang="scss" scoped>
// .van-tabs{
//     margin: -15px !important;
// }
.ora_content{
    padding: 10px;
    padding-bottom: 0px;
    border-radius: 20px;
    background: #fff;
    .van-cell{
        padding-bottom: 10px ;
        line-height: 15px;
    }
    .main{
        background: #fff;
        margin-bottom: 15px;
        .des{
            margin-top: 15px;
            font-size: 15px;
        }
        .content{
            margin-top: 20px;
            float: right;
            font-size: 8px;
        }
    }
    .btn{
        background: #fff;
        margin-top: 15px;
        height: 50px;
        border-top: 1px solid #f5f5f5;
        p{ 
            width: 80px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            color: red; 
           border: 1px solid red;
           border-radius: 20px;
